import {useEffect,useState} from "react"
import './App.css';

const App=() =>{

const [time,setTime]=useState(0);
const [target,setTarget]=useState(0);







const start=()=>{
   
    setInterval(()=>{
      setTime(a=>a+1)
        },1000)
  
        setTimeout(()=>{ window.responsiveVoice.speak("target complete")},target*1000)
  
}



  return (
    <div className="App">
      <a id="promo" href='https://www.freepik.com/photos/sports'>Sports photo created by tawatchai07 - www.freepik.com</a>
    <br/><br/><br/> <center><div className="center">
     
    <br/><br/><br/> 
      <input type="number" onChange={(e)=>setTarget(e.target.value)} placeholder="how much time?" id="time" className="time"/>
      <br/><br/> 
      <h1>{time}</h1>
      <br/><br/> 
      <button id="start" onClick={()=>start()} >start</button>
      </div></center> 
    </div>
  );
}

export default App;
